/* Specification */
body {
  margin: 0;
  padding: 0;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
.fa-search {
  color: #fff;
  font-size: 16px;
}
.fa-search:hover {
  font-size: 20px;
}
.container {
  max-width: 1130px;
  margin: 0 auto;
}
.scrolling {
  width: 40px;
  height: 40px;
  position: fixed;
  right: 40px;
  bottom: 20px;
  z-index: 1000;
}
.scrolling a {
  width: 100%;
  height: 100%;
  display: block;
  background-color: black;
  color: #fff;
  opacity: 0.7;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  font-size: 20px;
  line-height: 40px;
  text-align: center;
  border-radius: 25px;
}
.scrolling a:hover {
  opacity: 1;
  font-size: 24px;
}
/* MenuMobile */
.menu__mobile {
  display: none;
}
#hamburger {
  display: none;
  width: 35px;
  height: 40px;
  position: relative;
  -webkit-transform: rotate(0);
  transform: rotate(0);
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  z-index: 101;
  cursor: pointer;
}
.burger {
  display: block;
  position: absolute;
  height: 7px;
  width: 100%;
  background: #333;
  border-radius: 9px;
  opacity: 1;
  right: 10%;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}
.burger:nth-child(1) {
  top: 0;
}
.burger:nth-child(2) {
  top: 10px;
}
.burger:nth-child(3) {
  top: 20px;
}
#hamburger.open span:nth-child(1) {
  top: 8px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg);
}
#hamburger.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
}
#hamburger.open span:nth-child(3) {
  top: 8px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
}
.menu__mobile {
  display: none;
  width: 80%;
  position: fixed;
  box-shadow: 0 0 0 9999px rgba(0, 0, 0, 0.5);
  background: #f9f9f4;
  top: 0px;
  left: 0px;
  bottom: 0px;
  z-index: 100;
}
.menu__mobile ul {
  list-style: none;
  padding-left: 0;
  width: 80%;
}
.menu__mobile li {
  border-bottom: 2px solid #000;
  padding: 10px 10px 10px 30px;
  box-shadow: 2px 0 4px 0px;
  margin-bottom: 10px;
}
.menu__mobile a {
  text-decoration: none;
  color: #161616;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 20px;
}
/* Modal */
.overlay {
  z-index: 3;
  position: fixed;
  background-color: #000;
  opacity: 0.8;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
  display: none;
}
.modal {
  width: 600px;
  height: 290px;
  border-radius: 4px;
  background: #fff;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-transition: opacity 400ms ease-in;
  -moz-transition: opacity 400ms ease-in;
  transition: opacity 400ms ease-in;
  z-index: 5;
  display: none;
  margin: 5% auto;
}
