  height: 73px;
  background-color: #f43a55;
  border-radius: 50px;
  display: flex;
  outline: none;
  border: none;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  font-family: "Manjari";
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
}
.brand__main--imgBtnAll:hover {
  background-color: #d9344c;
  transition: 0.15s linear;
}
/* Product */
.product {
  height: 900px;
}
.product__title {
  text-align: center;
}
.product__title h1 {
  color: #161616;
  font-family: "Manjari";
  font-size: 42px;
  font-weight: 600;
  line-height: 40px;
  text-transform: uppercase;
  margin-bottom: 0;
}
.product__title h2 {
  font-size: 28px;
  margin-top: 10px;
  font-family: "Manjari";
  text-transform: uppercase;
}
.product__new {
  color: #f43a55;
  font-family: "Manjari";
  font-size: 28px;
  font-weight: 600;
  line-height: 46px;
  text-transform: uppercase;
}
.product__main {
  display: flex;
  justify-content: space-between;
}
.product__main--shoes {
  width: 381px;
  height: 343px;
  box-shadow: 0 0 16px rgba(107, 107, 107, 0.34);
  background-color: #ffffff;
  margin-right: 30px;
  display: inline-block;
  overflow: hidden;
}
.product__main--shoes:last-child {
  margin-right: 0;
}
.product__main--shoes img {
  transition: 1s;
  /* Время эффекта */
  display: block;
  /* Убираем небольшой отступ снизу */
}
.product__main--shoes:hover img {
  transform: scale(1.1);
  z-index: 101;
}
.product__main--shoesSticker {
  margin-left: 20px;
  display: flex;
  align-items: center;
}
.product__main--shoesSticker img {
  margin-right: 10px;
}
.product__main--shoesStickerSpan {
  margin-left: 100px;
  margin-top: -30px;
  color: #474341;
  font-family: "Manjari";
  font-size: 14px;
  font-weight: 600;
  line-height: 46px;
  text-transform: uppercase;
}
.product__main--shoesStickerSpan1 {
  margin-left: 150px;
  margin-top: -30px;
  color: #474341;
  font-family: "Manjari";
  font-size: 14px;
  font-weight: 600;
  line-height: 46px;
  text-transform: uppercase;
}
.product__main--shoesProduct {
  height: 230px;
  margin-left: 50px;
  cursor: pointer;
}
.product__main--infoText {
  padding-left: 50px;
}
.product__main--infoText p {
  color: #282422;
  font-family: "Manjari";
  font-size: 22px;
  font-weight: 600;
  line-height: 46px;
  margin-bottom: 10px;
  text-transform: uppercase;
}
.product__main--infoSpan {
  color: #939190;
  font-family: "Manjari";
  font-size: 17px;
  font-weight: 600;
  text-decoration: line-through;
  line-height: 24px;
  margin-left: 15px;
}
.product__main--infoSpanNew {
  color: #f43a55;
  font-family: "Manjari";
  font-size: 25px;
  font-weight: 600;
  line-height: 46px;
}
.product__main--Btn {
  margin: 0 auto;
  max-width: 224px;
  padding: 60px 0;
}
.product__main--BtnAll {
  width: 224px;
  height: 73px;
  background-color: #f43a55;
  border-radius: 50px;
  display: flex;
  outline: none;
  border: none;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  font-family: "Manjari";
  font-size: 20px;
  font-weight: 600;
  line-height: 48px;
  text-transform: uppercase;
}
.product__main--BtnAll:hover {
  background-color: #d9344c;
  transition: 0.15s linear;
}
/* Run */
.run {
  height: 900px;
  background: url(../../src/img/bg-km.png) no-repeat;
  background-position: center;
}
.run__main {
  padding: 20px 0;
}
.run__main--info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.run__main--info p {
  color: #161616;
  font-family: 'Roboto';
  font-size: 16px;
  font-weight: 600;
  margin: 0;
  line-height: 46px;
  text-transform: uppercase;
  margin-left: auto;
  margin-right: 45px;
}
.run__main--infoTitle {
  height: 230px;
  margin-left: auto;
}
.run__main--infoTitle img {
  margin: 55px 0;
}
.run__main--infoRound {
  margin-left: auto;
  margin-right: 63px;
  display: flex;
  flex-direction: column;
}
.run__main--infoRound img {
  margin-bottom: 18px;
  cursor: pointer;
}
.run__main--infoRound img:hover {
  transition: 0.5s;
  transform: scale(1.5);
}
.run__main--infoRound img:last-child {
  margin-bottom: 0;
}
.run__main--infoTitle span {
  color: #161616;
  font-family: "Roboto";
  font-size: 118px;
  font-weight: 400;
  line-height: 346.77px;
}
.run__main--arrow {
  margin-left: auto;
  margin-top: 330px;
  width: 30%;
}
.run__main--arrow span {
  color: #ffffff;
  font-family: "Manjari";
  font-size: 22px;
  font-weight: 400;
  line-height: 46px;
  display: flex;
  align-items: center;
  text-transform: uppercase;
}
.run__main--arrow img {
  margin-left: 25px;
  cursor: pointer;
}
.run__main--arrow img:hover {
  transition: 0.5s;
  transform: scale(1.2);
}
/* Letter */
.letter {
  height: 265px;
  background-color: #f43a55;
  opacity: 0.99;
}
.letter__main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px 0 20px;
}
.letter__main h4 {
  color: #ffffff;
  font-family: "Manjari";
  font-size: 30px;
  font-weight: 400;
  line-height: 18px;
}
.letter__main p {
  color: #ffffff;
  font-family: 'Roboto';
  width: 470px;
  font-size: 19px;
  font-weight: 400;
}
.letter__main--email input#address {
  width: 342px;
  height: 68px;
  border-radius: 50px;
  border: 1px solid #ffffff;
  font-family: "Manjari";
  background-color: #f43a55;
  outline: none;
  font-size: 19px;
  padding-left: 30px;
  color: #ffffff;
}
.letter__main--email input#address::-webkit-input-placeholder {
  color: #ffffff;
  font-family: "Manjari";
  font-size: 16px;
  font-weight: 400;
  font-style: italic;
  line-height: 46px;
  padding-left: 0px;
  text-transform: uppercase;
}
.letter__main--email button {
  width: 198px;
  height: 68px;
  border-radius: 50px;
  box-shadow: 20px 35px 34px 28px rgba(0, 0, 0, 0);
  background-color: #fffafb;
  color: #222121;
  font-family: "Manjari";
  font-size: 19px;
  font-weight: 600;
  outline: none;
  line-height: 46px;
  text-transform: uppercase;
  cursor: pointer;
  margin-left: 20px;
}
.letter__main--email button:hover {
  color: #fff;
  border: 2px solid #fff;
  background-color: #f43a55;
}
/* Footer */
.footer {
  height: 650px;
  background: linear-gradient(rgba(10, 1, 1, 0.52)), url('../../src/img/bg-footer.png') no-repeat;
  background-size: cover;
  background-position: center;
}
.footer__border {
  border-bottom: 1px solid #808080;
}
.footer__main {
  display: flex;
  padding-top: 110px;
  justify-content: space-around;
}
.footer__main p {
  color: #fa5016;
  font-family: "Manjari";
  font-size: 18px;
  font-weight: 600;
  line-height: 24px;
  text-transform: uppercase;
}
.footer__main ul {
  padding: 0;
  list-style: none;
}
.footer__main a {
  color: #ffffff;
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  text-decoration: none;
  text-transform: uppercase;
}
.footer__main a:hover {
  text-decoration: underline;
}
.footer__main--storeSpan {
  color: #ffffff;
  text-transform: capitalize;
  font-family: 'Roboto';
  font-size: 12px;
  font-weight: 400;
  line-height: 24px;
}
.footer__main--store span {
  color: #ffffff;
  margin-left: 15px;
  text-transform: lowercase;
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}
.footer__main--storeIcon {
  font-size: 20px;
  color: #f43c57;
  cursor: pointer;
}
.footer__main--storeIcon:hover {
  -webkit-transform: rotate(360deg);
  -moz-transform: rotate(360deg);
  -o-transform: rotate(360deg);
  -ms-transform: rotate(360deg);
  transition: all 0.5s ease;
  transform: rotate(360deg);
}
.footer__main--storeSocial {
  padding: 30px 0;
}
.footer__main--storeSocialIcon {
  color: #fff;
  font-size: 30px;
  margin-right: 15px;
  cursor: pointer;
}
.footer__main--storeSocialIcon:hover {
  opacity: 0.7;
}
.footer__main--logo {
  margin: 0 auto;
  max-width: 200px;
  padding-bottom: 50px;
}
.footer__span {
  padding-top: 40px;
  text-align: center;
}
.footer__span span {
  color: #ffffff;
  font-family: "Manjari";
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

